.page {
    background: url(/planets-fact-site-react/static/media/background-stars.f6233a6a.svg), #070724;
    color: white;
    margin: 0;
    padding: 0;
    font-family: "Spartan", sans-serif;
    margin: auto;
}


.header {
    display: flex;
    justify-content: space-between;
    top: 0;
    z-index: 9998;
    width: 100%;
    border-bottom: 1px solid rgba(255, 255, 255, 0.2);
}

.header__menu-btn {
    display: none;
}


.menu {
    display: flex;
    flex-direction: row;
    list-style: none;
    padding: 0;
    margin: 0;
    margin-right: 41px;
    min-height: 100%;
}

.header__planet-icon {
    display: none;
}

.header__arrow-icon {
    display: none;
}


.logo-link {
    text-decoration: none;
    margin: 22px auto 27px 32px;
    z-index: 1000;
}

.logo {
    margin: 0;
    font-family: "Antonio", sans-serif;
    text-transform: uppercase;
    color: white;
    font-size: 28px;
    line-height: 36.23px;
    letter-spacing: -1.05px;
    cursor: pointer;
    transition: opacity 0.4s ease;
}

.logo:hover {
    opacity: 0.7;
}

.header__list {
    list-style: none;
    margin-right: 33px;
    height: 100%;
}

.header__link {
    color: white;
    text-decoration: none;
    font-weight: 700;
    font-size: 11px;
    line-height: 86px;
    letter-spacing: 1px;
    text-transform: uppercase;
    cursor: pointer;
    transition: opacity 0.4s ease;
    min-height: 100%;
}

.header__link_active_mercury {
    border-top: 4px solid #419EBB; 
    line-height: 82px;
}

.header__link_active_venus {
    border-top: 4px solid #EDA249; 
    line-height: 82px;
}

.header__link_active_earth {
    border-top: 4px solid #6D2ED5; 
    line-height: 82px;
}

.header__link_active_mars {
    border-top: 4px solid #D14C32; 
    line-height: 82px;
}

.header__link_active_jupiter {
    border-top: 4px solid #D83A34; 
    line-height: 82px;
}

.header__link_active_saturn{
    border-top: 4px solid #CD5120; 
    line-height: 82px;
}

.header__link_active_uranus {
    border-top: 4px solid #1EC1A2; 
    line-height: 82px;
}

.header__link_active_neptune {
    border-top: 4px solid #2D68F0; 
    line-height: 82px;
}

.header__menu-info-group {
    display: flex;
    align-items: center;
    height: 100%;
}

.header__list:last-child {
    margin-right: 0;
}


