@media (max-width: 605px) {
    .header {
        flex-direction: row;
        position: relative;
    }
    
    .header__menu-icon {
        cursor: pointer;
        display: inline-block;
        padding: 28px 20px;
        position: relative;
        -webkit-user-select: none;
            -ms-user-select: none;
                user-select: none;
        align-self: center;
    }
      
    .navicon {
        background: white;
        display: block;
        height: 3px;
        position: relative;
        transition: background .2s ease-out;
        width: 25px;
        z-index: 1000;
    }
      
    .navicon:before,
    .navicon:after {
        background: white;
        content: '';
        display: block;
        height: 100%;
        position: absolute;
        transition: all .2s ease-out;
        width: 100%;
    }
      
    .navicon:before {
        top: 7px;
    }
      
    .navicon:after {
        top: -7px;
    }
      
    .header__menu-btn:checked ~ .header__menu-icon .navicon {
        background: transparent;
    }
      
    .header__menu-btn:checked ~ .header__menu-icon .navicon:before {
        -webkit-transform: rotate(-45deg);
                transform: rotate(-45deg);
    }
      
    .header__menu-btn:checked ~ .header__menu-icon .navicon:after {
        -webkit-transform: rotate(45deg);
                transform: rotate(45deg);
    }
      
    .header__menu-btn:checked ~ .header__menu-icon:not(.steps) .navicon:before,
    .header__menu-btn:checked ~ .header__menu-icon:not(.steps) .navicon:after {
        top: 0;
    }

    .menu {
        position: absolute;
        display: flex;
        flex-direction: column;
        margin: 0;
        padding: 0;
        list-style: none;
        background-color:#070724;
        top: 68px;
        bottom: 0;
        width: 100%;
        max-height: 0vh;
        overflow-y: hidden;
        z-index: 1;
        min-height: 0;
    }

    .menu-active {
        transition: 1s;
        max-height: 667px;
        min-height: 667px;
    }
    .menu-inactive {
        transition: 0.7s;
        max-height: 0vh;
    }

    .black-background {
        visibility: hidden;
        opacity: 0;
        position: fixed;
        width: 100%;
        height: 200vh;
        background-color: #070724;
        z-index: -1;
        transition: all 1s ease;
    }

    .black-background_active {
        visibility: visible;
        opacity: 1;
    }

    .header__list {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding-top: 20px;
        padding-bottom: 20px;
        border-bottom: 1px solid rgba(255, 255, 255, 0.2);
        max-width: 605px;
        margin: 0 auto;
        width: 87.2%;
    }

    .header__list_active_mercury {
        border-top: none; 
        padding-top: 20px;
    }
    
    .header__list_active_venus {
        border-top: none; 
        padding-top: 20px;
    }
    
    .header__list_active_earth {
        border-top: none; 
        padding-top: 20px;
    }
    
    .header__list_active_mars {
        border-top: none; 
        padding-top: 20px;
    }
    
    .header__list_active_jupiter {
        border-top: none; 
        padding-top: 20px;
    }
    
    .header__list_active_saturn{
        border-top: none; 
        padding-top: 20px;
    }
    
    .header__list_active_uranus {
        border-top: none; 
        padding-top: 20px;
    }
    
    .header__list_active_neptune {
        border-top: none; 
        padding-top: 20px;
    }

    .header__list:last-child {
        border-bottom: none;
        margin: 0 auto;
    }

    .header__menu-info-group {
        display: flex;
        align-items: center;
    }

    .header__link {
        margin-left: 20px;
        font-size: 15px;
        line-height: 42px;
        letter-spacing: 1.36px;
    }

    .header__planet-icon {
        display: block;
    }

    .header__arrow-icon {
        display: block;
        margin-right: 8px;
    }

    .logo-link {
        margin: 16px auto 16px 24px;
    }
}


/* Lead */

.lead {
    display: flex;
    justify-content: space-between;
    max-width: 1110px;
    width: 80%;
    margin: 126px auto 87px auto;
}

.lead__image-group {
    max-width: 667px;
    width: 59%;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
}

.lead__image-group_type_mobile {
    display: none;
}

.lead__geology-image {
    width: 163px;
    height: 199px;
    position: absolute;
    bottom: 0;
}

.image-active {
    display: block;
}

.lead__info-group {
    max-width: 350px;
    width: 37.2%;
}

.lead__title {
    font-family: "Antonio", sans-serif;
    font-weight: 400;
    font-size: 80px;
    line-height: 103.52px;
    text-transform: uppercase;
    margin: 0 0 23px 0;
}

.lead__description {
    font-weight: 400;
    font-size: 14px;
    line-height: 25px;
    margin: 0 0 24px 0;
}

.lead__source {
    font-weight: 400;
    font-size: 15px;
    line-height: 25px;
    opacity: .5;
    margin: 0 0 39px 0;
}

.lead__source-link {
    font-weight: 700;
    font-size: 14px;
    line-height: 25px;
    text-decoration: none;
    color: white;
}

.lead__button {
    width: 100%;
    height: 48px;
    border: 1px solid rgba(255, 255, 255, 0.2);
    background-color: transparent;
    color: white;
    display: flex;
    align-items: center;
    margin-bottom: 16px;
    text-transform: uppercase;
    padding: 0;
    cursor: pointer;
    transition: background-color 0.4s ease;
}

.lead__button:last-child {
    margin-bottom: 0;
}

.lead__button:hover {
    background-color: #D8D8D8;
}


.lead__button-number {
    font-weight: 700;
    font-size: 11px;
    letter-spacing: 1px;
    opacity: .5;
    margin: auto 28px;
}

.lead__button-text {
    font-weight: 700;
    font-size: 12px;
    line-height: 25px;
    letter-spacing: 2.57px;
}

